<template>
	<div class="body">
		<div class="company_name flex_between">
			<div class="company_name_left flex_start">
				<i class="icon iconfont icon-jijinyunhang1"></i>
				<span>{{name}}</span>
			</div>
		</div>

		<div class="cash_flow_table_box">
			<div class="fundRun-box">
				<div class="fundRun-body-box">
					<div class="fundRun-body-title flex_between">
						<div class="flex_start">
							<span class="circle_blue"></span>
							<span class="name">基金运行</span>
						</div>
						<div class="flex_start" v-if="showBtn">
							<div class="right">
								<label for="upload-fund-input">
									<div class="table-group-title-btn btn-blue-border">
										<span class="iconfont icon-daochu"></span>Excel导入
									</div>
								</label>
								<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadFundExcel($event)" />
							</div>
							<div class="right" @click="toDownloadExcel">
								<div class="table-group-title-btn btn-blue-border">
									<span class="iconfont icon-daoru"></span>Excel导出
								</div>
							</div>
							<div class="right" @click="downloadFundFile">
								<div class="table-group-title-btn btn-blue">
									<span class="iconfont icon-daoru"></span>下载基金模版
								</div>
							</div>
						</div>
					</div>

					<div class="fundRun-body-nav clear">
						<div class="fundRun-body-nav-box">
							<div v-for="(item,index) of navList" :key="index" class="fundRun-body-nav-item" :class="{'click':item.router==routerName}">
								<router-link :to="{name: item.router,query:{type: 1,id:id}}" >
									<span>{{item.name}}</span>
								</router-link>
							</div>
						</div>
					</div>

					<div class="fundRun-body-tab-box">
						<router-view  :key="activeDate" ></router-view>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		getFundExcel,
		getNewFundExcel,
		fundExcel,
		fundExcel2,
		downloadExcel,
		downloadExcel2,
		getNameApi,
	} from '../../config/api'
	import {AlreadycastproDownloadExcel,alreadyAdd} from '../../config/apiSj'
	import api from '../../config/url'; //引用url.js
	import {
		dialogConfirm,
		dialogConfirmHasbtn
	} from '../../config/mUtils'
	export default {
		data() {
			return {
				id: '',
				type:'',//1母基金2自管基金
				name: '',
				showBtn: false,
				activeDate:0,

				fundId: 4,
				step: 1,
				navList: [
					// {
					//    name: '基金资产和费用信息',
					//    step: 1,
					//    router:'motherFundRunDataMoneyInfo'
					// }, 
					// {
					//    name: '基金投资信息',
					//    step: 2,
					//    router:'motherFundRunDataInvestInfo'
					// }, 
					// {
					//    name: '已投项目情况',
					//    step: 3,
					//    router:'motherFundRunDataHaveInvested'
					// }, 
					{
						name: '投资组合',
						step: 4,
						router: 'motherFunRunDataInvestGroup',
					}, {
						name: '投资人',
						step: 5,
						router: 'motherFunRunDataInvestor',
					}, {
						name: '现金流',
						step: 6,
						router: 'motherFunRunDataCashFlow',
					}, 
//					{
//						name: '文件管理',
//						step: 7,
//						router: 'motherFundRunDataFileManage',
//					}
				],
				routerName: 'motherFunRunDataInvestGroup',
				
			}
		},
		watch: {
			'$route': 'isTab'
		},
		created() {
			this.routerName = this.$route.matched[5].name;
			this.id = this.$route.query.id;
			this.type = this.$route.query.type;
			this.getName();
			this.isTab();
			
		},
		computed: {},
		mounted() {},
		methods: {
			//导入Excel文件
			uploadFundExcel(e) {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				alreadyAdd({
					// fund_id: this.$route.query.id，
					type:this.type,
				}, file).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '导入成功');
						this.activeDate = new Date().getTime()
						inputDOM.value = '';
					} else {
						dialogConfirm('info', res.data.message);
						inputDOM.value = '';
					}
				})

			},
			//导出Excel文件
			toDownloadExcel() {
				// let table_token = window.localStorage.getItem("htcx_server_table_token");
				// window.location.href = api.url + '/web/fund/exportExcel?projId=' + this.$route.query.id + '&table_token=' + table_token;
				AlreadycastproDownloadExcel({
					id:this.id,
					type:this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res.data);
						window.location.href = res.data.payload.path;
					} else {
						dialogConfirm('info', res.data.message)
					}
				});
			},
			//下载模版
			downloadFundFile() {
				AlreadycastproDownloadExcel({
					id:this.id,
					type:this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res.data);
						window.location.href = res.data.payload.path;
					} else {
						dialogConfirm('info', res.data.message)
					}
				});
			},
			//监视路由
			isTab() {
				this.routerName = this.$route.matched[5].name;
				// console.log(this.routerName)
				// this.routerName == 'motherFundRunDataMoneyInfo' || this.routerName == 'motherFundRunDataInvestInfo' ||
				if( this.routerName == 'motherFundRunDataHaveInvested') {
					this.showBtn = true;
				} else {
					this.showBtn = false;
				}
			},
			getName(){
				getNameApi({
					id: this.id,
					type: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.name = res.data.payload.data.name;
					} else {
						dialogConfirm('info', res.data.message);
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../style/cashFlow.scss';
	.body {
		padding: 0 30px 20px 30px;
	}
	
	.cancle-btn {
		display: inline-block;
		width: 61px;
		height: 23px;
		background-color: #3C98E8;
		color: #ffffff;
		text-align: center;
		line-height: 23px;
		font-size: 14px;
		margin-left: 10px;
		margin-top: 20px;
		cursor: pointer;
	}
	
	.fundRun-box {
		//   min-width: 1100px;
		height: auto;
		//   padding-right: 16px;
	}
	
	.fundRun-body-box {
		width: 100%;
		background-color: #ffffff;
		margin-top: 20px;
		.fundRun-body-title {
			font-size: 16px;
			height: 30px;
			.circle_blue {
				width: 7px;
				height: 7px;
				border-radius: 50%;
				margin-right: 5px;
				background-color: #6292F4;
			}
			.name {
				color: #6292F4;
				font-size: 16px;
			}
			.search-name {
				font-size: 14px;
				padding-right: 10px;
				color: #35383A;
			}
		}
		.fundRun-body-nav {
			border-top: 1px solid #F1F1F1;
			border-bottom: 1px solid #F1F1F1;
			margin-top: 16px;
			.fundRun-body-nav-item {
				float: left;
				display: inline-block;
				margin: 0 32px;
				position: relative;
				top: 0px;
				border-bottom: 2px solid #ffffff;
				color: #A0A4AA;
				cursor: pointer;
				padding-top: 31px;
				padding-bottom: 20px;
				font-size: 14px;
			}
			.fundRun-body-nav-item:nth-child(1){
				margin-left: 0px;
			}
			.click {
				border-bottom: 2px solid #6292F4;
				color: #6292F4
			}
		}
		.fundRun-body-tab-box {
			margin-top: 20px;
			.fundRun-body-tab-title {
				margin-bottom: 10px;
				.cricle {
					display: inline-block;
					width: 6px;
					height: 6px;
					background-color: #3C98E8;
					border-radius: 3px;
					margin-right: 2px;
					margin-left: 20px;
					position: relative;
					top: -2px;
				}
				.fundRun-body-tab-title-name {
					font-size: 14px;
					font-weight: bold;
					color: #35383A;
				}
			}
			.table-normal {
				border: none;
			}
			.table-normal>.noColspanTr>th {
				text-align: left;
				color: #35383A;
				font-weight: 500;
				background-color: #F6FBFF;
				border: none;
			}
			.table-normal>.noColspanTr>td {
				text-align: left;
				border-right: none;
				position: relative;
				&>span {
					color: #6F7071;
					font-size: 14px;
				}
				input {
					width: 110px;
					height: 24px;
					border: 1px solid #F1F1F1;
					text-indent: 10px;
					border-radius: 3px;
					font-size: 14px;
					color: #6F7071;
				}
				input:focus {
					box-shadow: 0px 0px 2px 0px rgba(60, 152, 232, 1);
					border: 1px solid #3C98E8;
				}
				.showIcon {
					display: inline-block;
					width: 12px;
					height: 6px;
					background: url('../../images/slide_shape_off.png') no-repeat center;
					background-size: 100%;
					position: absolute;
					top: 50%;
					right: 18px;
					margin-top: -3px;
					transition: all 200ms linear;
					cursor: pointer;
				}
				.revolveShowIcon {
					transform: rotate(180deg)
				}
			}
			.table-normal>.secondData {
				background-color: #F6FBFF;
			}
			.table-normal>.secondData>td:nth-child(1) {
				&>span {
					padding-left: 30px;
				}
			}
		}
		.commit-btn-box {
			margin-top: 30px;
			.commit-btn {
				display: inline-block;
				width: 100px;
				height: 30px;
				font-size: 14px;
				text-align: center;
				line-height: 30px;
				background-color: #3C98E8;
				color: white;
			}
		}
		.fundRun-body-tab-item-box {
			margin-bottom: 20px;
		}
	} //excel文件导入导出按钮
	.table-group-title-btn {
		line-height: 30px;
		font-size: 14px;
		padding: 0px 10px;
		margin-right: 0px;
		margin-left: 10px;
		position: relative;
		top: 0px;
		&>span {
			display: inline-block;
			width: 20px;
			height: 20px;
			float: left;
			// margin-top: 5px;
			margin-right: 5px;
		}
		.icon_upload {
			background: url('../../images/icon_upload.png') no-repeat;
			background-size: 90%;
		}
		.icon_download {
			background: url('../../images/icon_download.png') no-repeat;
			background-size: 100%;
			// margin-top: 4px;
		}
	}
	
	.fundRun-body-tab-btn {
		display: flex;
		width: 61px;
		height: 23px;
		align-items: center;
		background-color: #3C98E8;
		justify-content: center;
		position: relative;
		top: 22px;
		cursor: pointer;
		img {
			width: 14px;
			height: 14px;
			margin-right: 2px;
		}
		span {
			color: #ffffff;
			font-size: 14px;
		}
	}
	
	.fundRun-body-tab-btn-box1 {
		margin-bottom: 10px;
		.fundRun-body-tab-btn {
			top: 0px;
		}
	}
</style>


